import React, { Component } from "react";
export default class Home extends Component {
  constructor(props) {
    super(props)
    this.state = {
      active: 0,
      navList: [{
        name: 'Popular',
        path: '/'
      },
      {
        name: 'Battle',
        path: '/battle'
      }]
    }
  }
  componentDidMount(){
    this.checkPage(Number(sessionStorage.tabKey || 0))
  }
  render() {
    const { active, navList } = this.state
    return (
      <div className='nav-page'>
        {
          navList.map((opt, i) => (<span key={i} className={Number(active) === i ? 'active' : ''} onClick={() => this.checkPage(i)}>{opt.name}</span>))
        }
      </div>
    )
  }
  checkPage=(i)=>{
    const { props }= this.props
    sessionStorage.tabKey = i
    this.setState({ active: i })
    if(i === 0){
      props.history.push('/')
    }else{
      props.history.push('/battle')
    }
  }
}